// export
module HelloWorld01

// import
open Elmish
open Elmish.React
open Fable.React

(*
    ElmElmishTEAmodel, update, view
    model, update, view
*)

module Types =
    type Model = Dummy

    type Msg = Dummy

module State =
    let init _ = Types.Dummy

    let update msg model = model

module View =
    // 
    // 
    let root (model: Types.Model) dispatch : ReactElement =
        str "Hello, World"

Program.mkSimple State.init State.update View.root
|> Program.withReactBatched "elmish-app"
|> Program.run